
import { defineComponent, ref } from 'vue'
import baseTab from '@/shared/components/Tab/BaseTab.vue'
import baseTabPan from '@/shared/components/Tab/BaseTabPan.vue'
import formatInput from '@/shared/components/FormatInput/FormatInput.vue'

export default defineComponent({
	components: {
		baseTab,
		baseTabPan,
		formatInput
	},
	data() {
		const tabHeaders = ref([
			{ name: "Lock/Unlock Service", disable: false },
			{ name: "Reset Password", disable: false },
			{ name: "Security Media/SMS Phone", disable: false },
			{ name: "Transfer Limit", disable: false },
			{ name: "Main Transfer Account", disable: false },
			{ name: "Withdraw Account", disable: false },
			{ name: "Re-Activate", disable: false },
			{ name: "IB Policy Configuration", disable: false },
			{ name: "Transfer Authorization", disable: false },
			{ name: "Edit E-mail", disable: false }
		]);
		const customerInfo = ref({
			cus_no: "",
			name: "",
			national_id: "",
			passport_id: "",
			customer_type: "",
			status: "",
			branch: "",
			nationality: "",
			tel: "",
			email: "",
			birthday: "",
			address: "",
			lock_unlock_service: {
				service: "",
				lock_reason: ""
			},
			reset_password: {
				email: "",
				error_count: 0
			},
			security_media_sms_phone: {
				media_type: "",
				phone_number: ""
			}
		})
		const tempcustomerInfo = ref({
			cus_no: "",
			name: "",
			national_id: "",
			passport_id: "",
			customer_type: "",
			status: "",
			branch: "",
			nationality: "",
			tel: "",
			email: "",
			birthday: "",
			address: "",
			lock_unlock_service: {
				service: "",
				lock_reason: ""
			},
			reset_password: {
				email: "",
				error_count: 0
			},
			security_media_sms_phone: {
				media_type: "",
				phone_number: ""
			}
		})
		const isEditable = ref(false)
		const isBeingEdited = ref(false)
		const oldTab = ref("")
		const newTab = ref("")

		return {
			customerInfo,
			tempcustomerInfo,
			tabHeaders,
			isEditable,
			isBeingEdited,
			oldTab,
			newTab,
			isResetPasswordDialogOpen: false,
			isLockUnlockServiceDialogOpen: false,
			isSecurityMediaDialogOpen: false,
			auth_code: "",
			isAllowSubmit: false,
		}
	},

	created() {
		this.app_requestCustomer()
	},

	methods: {
		// Common Methods
		handleCancelEditState() {
			this.isEditable = false
			this.isBeingEdited = false
			this.enableTabChangeAfterEdit()
		},
		handleEdit() {
			this.isEditable = true

			this.disableTabChangeOnEdit()
		},
		disableTabChangeOnEdit() {
			this.tabHeaders.forEach(e => {
				if (e.name !== this.oldTab) {
					e.disable = true
				}
			})
		},
		enableTabChangeAfterEdit() {
			this.tabHeaders.forEach(e => {
				e.disable = false
			})
		},
		onFormChange() {
			this.isBeingEdited = true
		},
		revertChanges() {
			this.tempcustomerInfo = JSON.parse(JSON.stringify(this.customerInfo))
		},
		confirmChanges() {
			this.customerInfo = JSON.parse(JSON.stringify(this.tempcustomerInfo))
		},
		app_requestCustomer() {
			this.$bizMOB.Network.requestTr({
				_bMock: true,
				_sTrcode: "CIB201003SC00",
				_fCallback: (res: any) => {
					if (res.header.result === false) {
						alert(`Error Code: ${res.header.error_code}, Error Text: ${res.header.error_text}`);
					} else {
						this.customerInfo = JSON.parse(JSON.stringify(res.body.userInfo));
						this.tempcustomerInfo = JSON.parse(JSON.stringify(res.body.userInfo));
					}
				}
			});
		},

		// CIB201003SC00_1T lock unlock service
		handleSubmitLockUnlockService() {
			this.confirmChanges()
			this.handleCancelEditState()
			this.$notify({
				group: "bottom-right",
				title: "Success",
				text: "Submit Successfully",
				type: 'success',
				speed: 1000
			})

			console.log(JSON.stringify(this.customerInfo))
		},
		handleLockUnlockServiceGoToList() {
			if (this.isBeingEdited) {
				this.isLockUnlockServiceDialogOpen = true
			} else {
				this.$router.push('/corporate-internet-banking/subscription')
			}
		},
		onCancelLockUnlockServiceDialog() {
			this.revertChanges()
			this.isLockUnlockServiceDialogOpen = false
			this.handleCancelEditState()
		},
		onConfirmLockUnlockServiceDialog() {
			this.handleSubmitLockUnlockService()
			this.isLockUnlockServiceDialogOpen = false
		},
		handleCancelLockUnlockServiceDialog() {
			if (this.isBeingEdited) {
				this.isLockUnlockServiceDialogOpen = true
			} else {
				this.handleCancelEditState()
			}
			// this.dialogService.onOpen({
			// 	isOpen: true,
			// 	title: 'Alert',
			// 	content: 'Do you want to save changes?'
			// 	actionsLeft: ,
			// 	actionsRight

			// })
		},

		// CIB201003SC00_2T reset password
		handleSubmitSendPassword() {
			if(this.tempcustomerInfo.reset_password.email === "" || this.tempcustomerInfo.reset_password.email === ""){
				this.$notify({
					group: "bottom-right",
					title: "Error Submitting",
					text: "Input Field Correctly",
					type: 'error',
					speed: 1000
				})
			}else{
				this.confirmChanges()
				this.handleCancelEditState()
				this.$notify({
					group: "bottom-right",
					title: "Success",
					text: "Submit Successfully",
					type: 'success',
					speed: 1000
				})
				console.log(this.customerInfo);
			}
		},
		openResetPasswordDialog() {
			this.isResetPasswordDialogOpen = true
		},
		onCancelResetPasswordDialog() {
			this.revertChanges()
			this.isResetPasswordDialogOpen = false
			this.handleCancelEditState()
		},
		onConfirmResetPasswordDialog() {
			this.confirmChanges()
			this.handleSubmitSendPassword()
			this.isResetPasswordDialogOpen = false
		},
		handleCancelResetPasswordDialog() {
			if (this.isBeingEdited) {
				this.isResetPasswordDialogOpen = true
			} else {
				this.handleCancelEditState()
			}
		},
		handleResetPasswordGoToList() {
			if (this.isBeingEdited) {
				this.isResetPasswordDialogOpen = true
			} else {
				this.$router.push('/corporate-internet-banking/subscription')
			}
		},

		// CIB201003SC00_3T security media
		handleCancelSecurityMediaDialog(){
			if (this.isBeingEdited) {
				this.isSecurityMediaDialogOpen = true
			} else {
				this.handleCancelEditState()
			}
		},
		handleSecurityMediaGoToList(){
			if (this.isBeingEdited) {
				this.isResetPasswordDialogOpen = true
			} else {
				this.$router.push('/corporate-internet-banking/subscription')
			}
		},
		handleSubmitSendSecurityMedia(){

			this.confirmChanges()
			this.handleCancelEditState()
			this.isAllowSubmit = false

			alert(JSON.stringify(this.customerInfo))
			console.log(this.customerInfo);
		},
		handleSendCodeToMobile() {
			if(this.tempcustomerInfo.security_media_sms_phone.phone_number == ""){
				alert(`Phone Number Invalid`)
			}else{
				alert(`Test message to ${this.tempcustomerInfo.security_media_sms_phone.phone_number}`)
			}
		},
		handleConfirmCodeFromMobile() {
			if(this.auth_code == '837020')
			{
				this.isAllowSubmit = true
				alert("authentication is successful")
			}else{
				alert("authentication error")
			}
		}
	}
})
